/* ranchers-regular - latin */
@font-face {
  font-family: 'Ranchers';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/ranchers/ranchers-v8-latin-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
    url('../fonts/ranchers/ranchers-v8-latin-regular.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('../fonts/ranchers/ranchers-v8-latin-regular.woff2')
      format('woff2'),
    /* Super Modern Browsers */
      url('../fonts/ranchers/ranchers-v8-latin-regular.woff') format('woff'),
    /* Modern Browsers */ url('../fonts/ranchers/ranchers-v8-latin-regular.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('../fonts/ranchers/ranchers-v8-latin-regular.svg#Ranchers')
      format('svg'); /* Legacy iOS */
}

